import {View, Text, TouchableOpacity, ScrollView, Modal, TextInput, RefreshControl,SafeAreaView, Alert, KeyboardAvoidingView} from 'react-native-web';
import {useState, useEffect} from 'react';
import  {DataStore} from '@aws-amplify/datastore';
import {Restaurant, Member,} from '../models';
import { styles, colorPack,  height, map_darkStyle } from '../style/style';
import MapView, { Marker, PROVIDER_GOOGLE } from 'react-native-web-maps';
import * as Linking from 'expo-linking';

export default function Restaurant_page_guest({route, navigation}){
    
    // console.log('Restaurant_page_guest', route);

    const user = route.params.user;//{username: 'test', email: ''}
    const [restaurant, setRestaurant] = useState(route.params.restaurant);//{makerID: 'test', name: '', fee: 0, num_members: 0, menu: [], isFinishRecruiting: false}
    const place = route.params.place;//{name: '', latitude: 0, longitude: 0}
    

    const [modalVisible, setModalVisible] = useState(false);
    const [menuList, setMenuList] = useState('  ');
    const [menuPrice, setMenuPrice] = useState(0);
    const [isRegistered, setIsRegistered] = useState(false);

    const[refreshing, setRefreshing] = useState(false);
    
    useEffect(() => {
        getMembers(); // get member from database
        // console.log('user', user)
        // console.log('member', member)
        // console.log('restaurant', restaurant)
        // console.log('place', place)
    }, [isRegistered, modalVisible]);
    
    const getMembers = async () => {
        setRefreshing(true);

        const members = await DataStore.query(Member, member=>member.restaurantID('eq', restaurant.id));
        // console.log('members', members)
        const _membersList = []
        members.forEach(async (m, index) => {
            const _m = Members(user, m, restaurant, index)
            
            _membersList.push(_m)
            if(m.username==user.username){
                setIsRegistered(true)
                // console.log('!')
                setMenuList(m.menu.join(', '))
                setMenuPrice(m.price)
            }
        })
        setRestaurant({...restaurant, num_members: members.length})
        setMembersList(_membersList)
        setRefreshing(false)

    }

    const makeNewMember = async () => {
        const _isRegistered = await DataStore.query(Member, member => member.username("eq", user.username).restaurantID("eq", restaurant.id))
        // console.log(_isRegistered)
        setIsRegistered(_isRegistered.length>0)
        if(!isRegistered){
            await DataStore.save(
                new Member({
                    "username": user.username,
                    "email": user.email,
                    "phone_number": user.phone_number,
                    "menu": ['  '], 
                    "fee":Number(0),
                    "restaurantID": restaurant.id,
                })
            );

            const CURRENT_ITEM = await DataStore.query(Restaurant, restaurant.id);
            const updatedItem = await DataStore.save(Restaurant.copyOf(CURRENT_ITEM, updated => {
                // Update the values on {item} variable to update DataStore entry
                updated.num_members = updated.num_members +1;
                }));
            // console.log('  .', updatedItem)
            setRestaurant(updatedItem)

            setModalVisible(true);
            // refreshRestaurantList(id=place.id);
        }
        else{
            // Alert.alert('',' .',[{text:'', onPress:()=>{setModalVisible(true)}},{text:'', onPress:()=>{}}])
            setModalVisible(true);
        }
    }

    const deleteMember = async () => {
        if(user.username !== restaurant.makerID){
        //      
            try {
                const _member = await DataStore.delete(Member, member => member.username("eq", user.username).restaurantID("eq", restaurant.id)); // delete member
                
                // console.log('memberLength', _member.length)
                if(_member.length>0){ //     
                    const CURRENT_ITEM = await DataStore.query(Restaurant, restaurant.id);
                    // console.log('CURRENT_ITEM', CURRENT_ITEM)
                    await DataStore.save(Restaurant.copyOf(CURRENT_ITEM, updated => {
                    // Update the values on {item} variable to update DataStore entry
                    updated.num_members -= 1; ;
                    }));
                    navigation.goBack();
                    // refreshRestaurantList(id=restaurant.placeID);
                    setIsRegistered(false);
                }
                else{
                    Alert.alert('','  .',[{text:''}])
                }
            } catch (error) {
                // console.log(error)
                if(error.code === 'ConcurrentModificationException'){
                    Alert.alert('','   .',[{text:''}])
                }
                else if(error.code === 'NotFoundException'){
                    Alert.alert('','   .',[{text:''}])
                }
                else{
                    // console.log('  ')
                    alert('  ')
                }
            }
        }
        else{
            Alert.alert('','     .',[{text:''}])
        }
    }

    const addMenu = async () => { //        
        // console.log('addMenu')

        const _menuList = menuList.split('\n');
        // console.log('_menuList', _menuList)
        // console.log('menuPrice', menuPrice)

        const CURRENT_Member = await DataStore.query(Member, member => member.username("eq", user.username).restaurantID("eq", restaurant.id));
        // console.log('current member', CURRENT_Member[0])
        try {await DataStore.save(Member.copyOf(CURRENT_Member[0], updated => {
            // Update the values on {item} variable to update DataStore entry
            updated.menu = _menuList;
            updated.price = menuPrice;
        }));

        setModalVisible(false)}
        catch (error) {
            // console.log(error)
        }
    }

    const [membersList, setMembersList] = useState(
        [
            
        ]
    );

    return (
        <View style={styles.container}>

        <Modal animationType='fade'
        transparent={false}
        visible={modalVisible}
        onRequestClose={() => {
          setModalVisible(false);
        }}
        >
            <KeyboardAvoidingView behavior='padding' keyboardVerticalOffset={height*50/2000}
            style={styles.restaurantInfoModal}>

            <View style={[styles.header,{opacity:0.5}]}>
                <Text style={styles.highlightText}>
                    {restaurant.name}
                </Text>
                <Text style={styles.highlightText}>
                    {restaurant.num_members==0?`  ${restaurant.fee}`:`: ${restaurant.fee} / ${restaurant.num_members} = ${Math.ceil(restaurant.fee/restaurant.num_members)}`}
                </Text>
            </View>
            
            <View style={styles.header}>
            <Text style={styles.highlightText}>
                {' '}
            </Text>
            </View>


            <ScrollView>
            <View style={styles.restaurantPageContainerModal}>


          <View style={{
            flexDirection:'row',
          }}>

            <TextInput 
            style={[styles.textInputBox_restaurant_menu, styles.highlightText,{borderWidth:0}]}
            editable={false}
            placeholder={' '}
            placeholderTextColor={colorPack.text_dark}
            ></TextInput>

            <TextInput 
            style={[styles.textInputBox_restaurant_price, styles.normalText,{borderWidth:0}]}
            editable={false}
            placeholder={'()'}
            numberOfLines={2}
            placeholderTextColor={colorPack.text_dark}
            ></TextInput>

          </View>

          <View style={{
            flexDirection:'row',
            alignItems:'center',
          }}>

            <TextInput 
            style={[styles.textInputBox_restaurant_menu, styles.normalText, {textAlign:'left'}]}
            multiline={true}

            placeholder={'  .\n   \n   .\n   \n   .'}
            placeholderTextColor={colorPack.deactivated}
            onChangeText={(text) => {setMenuList(text)}}
            ></TextInput>

            <TextInput 
            style={[styles.textInputBox_restaurant_price, styles.normalText,]}
            placeholderTextColor={colorPack.deactivated}
            keyboardType='numeric'
            onChangeText={(text)=>{
                setMenuPrice(parseInt(text))
            }}
            placeholder={' '}
            ></TextInput>

        </View>


          <View style={{flexDirection:'row', marginVertical:height*100/2000}}>
          
          <TouchableOpacity
          style = {styles.modalButton}
          onPress={()=>
            
            {setModalVisible(false);}}

          >
          <Text style={styles.highlightText}>
          {''}
          </Text>
            </TouchableOpacity>

            <TouchableOpacity
          style = {styles.modalButton}
          onPress={()=>{
                if(menuList && menuPrice){
                    
                    addMenu();
                }
                else{
                    Alert.alert('','    .',[{text: ''}])
                }
            }}
            >
          <Text style={styles.highlightText}>
          {'  '}
          </Text>
            </TouchableOpacity>

          </View>
          
          
          </View>

          </ScrollView>
          </KeyboardAvoidingView>
        </Modal>




            <View style={styles.header}>
                <Text style={styles.highlightText}>
                    {restaurant.name}
                </Text>
                <Text style={styles.highlightText}>
                    {restaurant.num_members==0?`  ${restaurant.fee}`:`: ${restaurant.fee} / ${restaurant.num_members} = ${Math.ceil(restaurant.fee/restaurant.num_members)}`}
                </Text>
            </View>


            <View style={styles.restaurantButtonContainer}>
                <TouchableOpacity style={styles.restaurantButton_1}
                onPress={() => {
                    Linking.openURL(restaurant.url);
                }}
                >
                    <Text style={styles.normalText}>
                        {'\n'}
                    </Text>
                </TouchableOpacity>

                <TouchableOpacity style={styles.restaurantButton_2}
                    onPress={() => makeNewMember()}
                >
                    <Text style={styles.normalText}>
                        {(isRegistered)?' ':''}
                    </Text>
                </TouchableOpacity>

                <TouchableOpacity style={styles.restaurantButton_1}
                disabled={true}
                >
                    <Text style={(styles.deactivatedText)}>
                        {' \n'}
                    </Text>
                </TouchableOpacity>

                <TouchableOpacity style={styles.restaurantButton_2}
                onPressOut={() => deleteMember()}
                disabled={!isRegistered}
                >
                <Text style={(!isRegistered)?(styles.deactivatedText):(styles.normalText)}>
                    {''}
                </Text>
                </TouchableOpacity>

            </View>


            <View style={styles.mapContainer} >
              <MapView
              provider={PROVIDER_GOOGLE}
              customMapStyle={map_darkStyle}
              style={styles.map}
              initialRegion={{longitude: place.longitude, latitude: place.latitude, latitudeDelta: 0.003, longitudeDelta: 0.003}}
              showsMyLocationButton={false}
              showsUserLocation={true}
              loadingEnabled={true}
              zoomEnabled={true}
              rotateEnabled={true}
              >
                <Marker
                    coordinate={{longitude: place.longitude, latitude: place.latitude}}
                    title={place.name}
                    description={`${place.num_restaurants} `}
                    key={place.id}
                />
              </MapView>
            
            </View>
            <SafeAreaView>

            <ScrollView style={styles.restaurantListContainer}
            refreshControl={
              <RefreshControl
                refreshing={refreshing}
                onRefresh={()=>{
                  getMembers();
                }
                }
              />
              }
            >
              
            {membersList}
            </ScrollView>
            </SafeAreaView>
        </View>

    );
}


function Members(user, member, restaurant, index){

//    console.log('Members', user, member, restaurant, index)

    const backgroundColor_odd = colorPack.highlight_dark
    const backgroundColor_even = colorPack.highlight_light
    var myBackgroundColor
    if(Number(index) %2 == 0){
        myBackgroundColor = backgroundColor_even
    }
    else{
        myBackgroundColor = backgroundColor_odd
    }
    return(
        
        <TouchableOpacity style={[styles.restaurantList,{backgroundColor:myBackgroundColor}]} key={member.id}
        disabled={true}
        >

            <Text style={[styles.highlightText, styles.restaurantFee]}
            ellipsizeMode='tail'
            numberOfLines={1}
            >{member.username===user.username?' ':member.email.split('@')[0]}
            </Text>

            <TouchableOpacity 
            onPress={()=>{
                Alert.alert(`${member.email.split('@')[0]}  `,`${member.menu}\n: ${member.price}, : ${restaurant.fee/restaurant.num_members}`, [{text:''}])
            }}
            >
            <Text style={[styles.normalText,styles.restaurantName]}>{`${member.menu[0]}  ${member.menu.length}`}</Text>
            </TouchableOpacity>


            <Text style={[styles.normalText, styles.restaurantMembers]}>{Math.ceil(member.price + (restaurant.fee/restaurant.num_members))+''}</Text>

        </TouchableOpacity>
    )
}