import {View, Text, TouchableOpacity} from 'react-native-web';
import { colorPack, styles, width } from '../style/style';


function Main_restaurantList(user, restaurant, num, navigation, place, setRestaurantList, restaurantList, refreshRestaurantList) {
  const backgroundColor_odd = colorPack.highlight_dark
  const backgroundColor_even = colorPack.highlight_light
  var myBackgroundColor

  // console.log('Main_restaurantList', user, restaurant)
  if(Number(num) %2 == 0){
    myBackgroundColor = backgroundColor_even
  }
  else{
    myBackgroundColor = backgroundColor_odd
  }


  // console.log(restaurantList)

 // return 
  return (restaurant.isFinishRecruiting?
    (<View style={[styles.restaurantList,{backgroundColor:myBackgroundColor}]} 
    key={restaurant.id}   
    >
    <TouchableOpacity
    onPress=  {() => {  
      // console.log(JSON.stringify(user))
      navigation.navigate('Restaurant', {user:user, restaurant:restaurant, place:place})
      // console.log('pressed')
    }}
    >

      <Text style={[styles.deactivatedText, styles.restaurantName]} numberOfLines={1} ellipsizeMode='tail' 
      >{user.username == restaurant.makerID?`"${restaurant.name}"`:restaurant.name}</Text>

    </TouchableOpacity>
      <Text style={[styles.normalText, styles.restaurantFee,]} ellipsizeMode='tail' numberOfLines={1}>{''}</Text>

      <Text style={[styles.normalText, styles.restaurantMembers]} ellipsizeMode='tail' numberOfLines={1}>{``}</Text>

    </View>)
    :
    (<View style={[styles.restaurantList,{backgroundColor:myBackgroundColor}]} 
    key={restaurant.id}   
    >
    <TouchableOpacity
    onPress=  {() => {
      navigation.navigate('Restaurant', {user:user, restaurant:restaurant, place:place})
      // console.log('pressed')
    }}>

      <Text style={[styles.highlightText, styles.restaurantName]} numberOfLines={1} ellipsizeMode='tail' 
      >{user.username == restaurant.makerID?`"${restaurant.name}"`:restaurant.name}</Text>

      </TouchableOpacity>
      <Text style={[styles.normalText, styles.restaurantFee,]} ellipsizeMode='tail' numberOfLines={1}>{restaurant.num_members==0?` ${restaurant.fee}`:` ${restaurant.fee/restaurant.num_members}`}</Text>

      <Text style={[styles.normalText, styles.restaurantMembers]} ellipsizeMode='tail' numberOfLines={1}>{`${restaurant.num_members}`}</Text>

    </View>)
  );  // return
}

function Main_restaurantList_sample(id, name, fee, num) {
  const backgroundColor_odd = colorPack.highlight_dark
  const backgroundColor_even = colorPack.highlight_light
  var myBackgroundColor
  if(Number(num) %2 == 0){
    myBackgroundColor = backgroundColor_even
  }
  else{
    myBackgroundColor = backgroundColor_odd
  }
 // return 
  return (
    <TouchableOpacity style={[styles.restaurantList,{backgroundColor:myBackgroundColor}]} key={id}
    disabled={true}
    >

      <Text style={[styles.normalText, styles.restaurantName]}>{name}</Text>

      <Text style={[styles.deactivatedText, styles.restaurantFee]}>{`${fee}`}</Text>
      
    </TouchableOpacity>
  );  // return
}

export {Main_restaurantList, Main_restaurantList_sample}